.main {
  &__title {
    padding-top: 250px;
    font-size: 85px;
    width: 100%;
    text-align: center;
    color: rgb(0, 0, 0);
    opacity: .7;
  }

  &__menu {
    text-align: center;
    margin-top: 30px;
    font-size: 22px;
    opacity: .6;
    background: rgb(0, 0, 0);
    padding-top: 10px;
    padding-bottom: 10px;
    color: white;
  }

  &__menu ul li{
    display: inline;
    margin-right: 30px;
  }

  &__menu ul li:last-child {
    margin-right: 0;
  }
}

/* タブレットを想定 */
@media only screen and (max-width:960px) {
.title {
  padding-top: 250px;
  font-size: 75px;
  width: 100%;
  text-align: center;
  color: rgb(0, 0, 0);
  opacity: .7;
}

.menu {
  text-align: center;
  margin-top: 30px;
  font-size: 18px;
  opacity: .6;
  background: rgb(0, 0, 0);
  padding-top: 10px;
  padding-bottom: 10px;
  color: white;
}

}

/* スマホを想定 */
@media screen and (max-width: 480px) {
.title {
  padding-top: 250px;
  font-size: 60px;
  width: 100%;
  text-align: center;
  color: rgb(0, 0, 0);
  opacity: .7;
  }

.menu {
  text-align: center;
  margin-top: 30px;
  font-size: 16px;
  opacity: .6;
  background: rgb(0, 0, 0);
  padding-top: 10px;
  padding-bottom: 10px;
  color: white;
}
}
